import React from 'react'
import { useState } from 'react'
import { Drawer, List, ListItemButton, ListItemText, ListItemIcon, IconButton, Tab } from '@mui/material'
import TextSnippetIcon from '@mui/icons-material/TextSnippet';
import InfoIcon from '@mui/icons-material/Info';
import MenuIcon from '@mui/icons-material/Menu';


const DrawerComp = () => {
    const [openDrawer, setOpenDrawer] = useState(false);


    return (
        <>
            <Drawer open={openDrawer}
                sx={{ background: "#062925", fontSize: 64 }}
                onClose={() => setOpenDrawer(false)}
            >
                <List sx={{ background: "#3A9188", color: "#062925" }}>


                    <ListItemButton onClick={() => setOpenDrawer(false)}>
                        <Tab sx={{ fontSize: 20, fontWeight: 900 }} className="tablist" label="HOME" href='#home' ></Tab>
                    </ListItemButton>
                    <ListItemButton onClick={() => setOpenDrawer(false)}>
                        <Tab sx={{ fontSize: 20, fontWeight: 900 }} className="tablist" label="EXPERIENCE" href='#experience' />
                    </ListItemButton>
                    <ListItemButton onClick={() => setOpenDrawer(false)}>
                        <Tab sx={{ fontSize: 20, fontWeight: 900 }} className="tablist" label="PROJECT" href='#project' />
                    </ListItemButton>
                    <ListItemButton onClick={() => setOpenDrawer(false)}>
                        <Tab sx={{ fontSize: 20, fontWeight: 900 }} className="tablist" label="CONTACT_US" href='#contact' />
                    </ListItemButton>
                    <ListItemButton onClick={() => setOpenDrawer(false)}>
                        <Tab sx={{ fontSize: 20, fontWeight: 900 }} className="tablist" label="ABOUT_ME" href='#about' />
                    </ListItemButton>
                    <ListItemButton /> <ListItemButton /> <ListItemButton /> <ListItemButton />  <ListItemButton /> <ListItemButton /><ListItemButton />
                    <ListItemButton /> <ListItemButton /> <ListItemButton /> <ListItemButton />  <ListItemButton /> <ListItemButton /><ListItemButton />
                    <ListItemButton /> <ListItemButton /> <ListItemButton /> <ListItemButton />  <ListItemButton /> <ListItemButton /><ListItemButton />
                    <ListItemButton /> <ListItemButton /> <ListItemButton /> <ListItemButton />  <ListItemButton /> <ListItemButton /><ListItemButton />
                    <ListItemButton /> <ListItemButton /> <ListItemButton /> <ListItemButton />


                </List>
            </Drawer>

            <IconButton sx={{ marginLeft: "auto", background: "#3A9188", color: "#062925" }} onClick={() => setOpenDrawer(!openDrawer)}>
                <MenuIcon />
            </IconButton>

        </>
    )
}

export default DrawerComp
